














































import uniList from '@/components/list/uni-list/uni-list.vue'
import uniIcon from '@/components/list/uni-icon/uni-icon.vue'
import uniListItem from '@/components/list/uni-list-item/uni-list-item.vue'
import config from '../../util/config.js';
import request from '../../util/request.js';
import service from '../../service.js';
import {
    mapState,
	mapMutations
} from 'vuex'
export default {
	computed: mapState(['sublistItem', 'sublistIndex', 'userInfo', 'inputPageText', 'key']),
	components: {uniList,uniListItem,uniIcon},
	data() {
	    return {
			// 当页面OnShow的时候是否需要从state里去拿输入的内容
			needGetInputOnShow: false,
			// 上传照片相关
			imageViewHeight: 100,
			imageList: [],
			littleImageWidth: 0,
			
			// 页面参数，接口需要的参数
			dangerId: '',		// 隐患id
			dangerLevel: '',	// 隐患级别
			dangerType:	'',		// 隐患分类（隐患后果）
			dangerDesc: '',		// 隐患描述
			dangerClause: '',	// 对应条款
			dangerAdvise: '',	// 整改建议
			
			checkDate: '',		// 检查时间
			checkPeople: '',	// 检查人
			deadLine: '',		// 整改期限
			
			dangerDept:'',		//隐患责任部门
			
			// --------数据模板---------
			// 隐患级别
			dangerLevelData: ['一般隐患', '重大隐患'],
			// 隐患分类
			//dangerTypeData: ['自行输入分类','从分类库选择'],
			//隐患后果
			dangerTypeData: ['人员伤害','财产损失','其他'],
			// 隐患描述
			dangerDescData: ['从隐患库选择/检索','自行输入问题', '从常用隐患库选择'],
			// 对应条款
			dangerClauseData: ['从隐患库选择/检索','自行输入问题', '从法规库选择'],
			//隐患责任部门
			dangerDeptData: ['从隐患库选择/检索','自行输入问题', '从部门库选择'],
		}
	},
	onLoad() {
		this.littleImageWidth = (uni.getSystemInfoSync().windowWidth -50) / 4;
		this.checkPeople = this.userInfo.fullname;
	},
	onNavigationBarButtonTap() {
		
	},
	onShow() {
		if (this.needGetInputOnShow == true) {
			this.$data[this.key] = this.inputPageText;
			this.needGetInputOnShow = false;
			this.removeInputItem();
		}
	},
	methods:{
		...mapMutations(['removeInputItem']),
		// 选择隐患级别
		selectedDangerLevel: function(e) {
			var that = this;
			uni.showActionSheet({
				itemList: that.dangerLevelData,
				success: function (res) {
					that.dangerLevel = that.dangerLevelData[res.tapIndex];
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		},
		// 选择隐患责任部门
		selectedDept: function(e) {
			var that = this;
			uni.showActionSheet({
				itemList: that.dangerDeptData,
				success: function (res) {
					if (res.tapIndex == 0) {// 从隐患库选择/检索
						
					}else if(res.tapIndex == 1) {// 自行输入
						this.needGetInputOnShow = true;
						that.jumpInput(that.dangerDept, 'dangerDept', '请输入隐患责任部门')
					}else {// 从部门库选择
						
					}
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		},
		// 选择隐患分类
		selectedDangerType: function(e) {
			var that = this;
			/* uni.showActionSheet({
				itemList: that.dangerTypeData,
				success: function (res) {
					if (res.tapIndex == 0) {// 自行输入
						this.needGetInputOnShow = true;
						that.jumpInput(that.dangerType, 'dangerType', '请输入隐患分类')
					}else {// 从分类库选择
						
					}
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			}); */
			
			uni.showActionSheet({
				itemList: that.dangerTypeData,
				success: function (res) {
					that.dangerType = that.dangerTypeData[res.tapIndex];
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		},
		// 选择隐患描述
		selectedDangerDesc: function(e) {
			var that = this;
			uni.showActionSheet({
				itemList: that.dangerDescData,
				success: function (res) {
					if (res.tapIndex == 0) {// 从隐患库选择/检索
						
					}else if(res.tapIndex == 1) {// 自行输入
						this.needGetInputOnShow = true;
						that.jumpInput(that.dangerDesc, 'dangerDesc', '请输入隐患描述')
					}else {// 从常用隐患库选择
						
					}
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		},
		// 选择对应条款
		selectedDangerClause: function(e) {
			var that = this;
			uni.showActionSheet({
				itemList: that.dangerClauseData,
				success: function (res) {
					if (res.tapIndex == 0) {// 从隐患库选择/检索
						
					}else if(res.tapIndex == 1) {// 自行输入
						this.needGetInputOnShow = true;
						that.jumpInput(that.dangerClause, 'dangerClause', '请输入对应条款')
					}else {// 从法规库选择
						
					}
				},
				fail: function (res) {
					console.log(res.errMsg);
				}
			});
		},
		// 选择检查日期
		checkDateChange: function(e) {
			this.checkDate = e.target.value;
		},
		// 选择整改期限
		deadLineChange: function(e) {
			this.deadLine = e.target.value;
		},
		// 保存
		saveClick() {
			var that = this;
			let param = {
				refid: that.dangerId,
				yhjb: that.dangerLevel,
				yhfl: that.dangerType,
				yhms: that.dangerDesc,
				dytk: that.dangerClause,
				zgjy: that.dangerAdvise,
				jcsj: that.checkDate,
				jcr: that.checkPeople,
				zgqx: that.deadLine,
				zrbm:that.dangerDept,
				userid: that.userInfo.userid
			}
			request.requestLoading(config.editDanger, param, '正在加载', 
				function(res){
					uni.showToast({
						icon: 'none',
						title: '保存成功',
						duration: 1000,
					});
					that.dangerId = res.id;
				},function(){
					uni.showToast({
						icon: 'none',
						title: '保存失败'
					});
				},function() {
					
			});
		},
		jumpInput(text, key, placeholder = '请输入描述') {
			this.needGetInputOnShow = true;
			uni.navigateTo({
				url: '../common/inputPage?text=' + text + '&key=' + key + '&placeholder=' + placeholder + '',
			})
		},
		// 上传照片功能-添加照片
		addPhoto() {
			var that = this;
			uni.chooseImage({
				count: 9, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album', 'camera'], //从相册选择
				success: function (res) {
					console.log(JSON.stringify(res.tempFilePaths));
					for (var i=0;i<res.tempFilePaths.length;i++) {
						var imgObj = {//	type：1为新增需要上传，2为加载的，不需要上传
							fileid: '',
							src: res.tempFilePaths[i],
							type: 1
						}
						that.imageList.push(imgObj);
					}
				}
			});
		},
		// 删除照片，需要分两种情况，如是从后台加载的，那需要调用删除接口，如果是直接本地读取还未上传的，不需要调删除接口
		deleteImage(imgObj, index) {
			var that = this;
			if (imgObj.src.startsWith('http:')) {// 网络图片
				let obj = {
					item: that.item,
					index: that.itemIndex
				}
				that.setSublistItem(obj);
				
				let param = {
					from: 'jc',
					yyid: that.item.id,
					fileid: imgObj.fileid,
					userid: that.userInfo.userid
				};
				request.requestLoading(config.deleteImage, param, '正在删除图片', 
					function(res){
						console.log('删除成功：' + JSON.stringify(res));
						that.item.fj = res.fj
						that.imageList.splice(index,1);
					},function(){
						uni.showToast({
							icon: 'none',
							title: '删除失败'
						});
					}, function(){
						
					}
				);
			}else {// 刚选择好，还未上传，非网络图片
				that.imageList.splice(index,1);
			}
		},
		// 浏览照片
		viewPhoto() {
			var that = this;
			var imgList = []
			for (var i=0 ; i<that.imageList.length; i++) {
				let item = that.imageList[i]
				imgList.push(item.src);
			}
			// 预览图片
			uni.previewImage({
				urls: imgList
			});
		},
	}
}
